@import url('https://fonts.googleapis.com/css?family=Roboto');

html, body {
    font: 3vh "Roboto", sans-serif;
    height: 100%;
    font-size: calc(14px + (26 - 14) * ((100vw - 300px) / (1600 - 300)));
    line-height: calc(1.3em + (1.5 - 1.2) * ((100vw - 300px)/(1600 - 300)));
}

body {
    background-image: url(bg.jpg);
    background-position: center;
    background-repeat:  no-repeat;
    background-attachment: fixed;
    background-size:  cover;
    background-color: #999;
    overflow:hidden;
    text-align: center;
    font-weight: bold;
}

ol, ul {
    padding-left: 30px;
}

.profile > img {
    display: block;
    margin-top: 2.5%;
    margin-left: auto;
    margin-right: auto;
    width: 15%;
    border-radius: 25%;
}

.profile > p {
    font-size: 125%;
}

.links {
    display: block;
    margin-top: 2.5%;
    margin-left: auto;
    margin-right: auto;
    width: 20%;
    border-radius: 25%;
    font-size: 75%;
}

.projects > table {
    border-spacing: 3vw;
    font-size: 75%;
    font-weight: normal;
}